<style>
#Customsbox .title {
  line-height: 24px;
  border-left: 5px solid #2d8cf0;
  font-weight: bold;
  font-size: 16px;
  text-indent: 10px;
  margin-bottom: 10px;
}
#Customsbox .subCol ul li {
  margin: 0 -18px;
  list-style: none;
  text-align: center;
  padding: 9px;
  border-bottom: 1px solid #ccc;
  overflow-x: hidden;
}
#Customsbox .subCol ul li:last-child {
  border-bottom: none;
}
#Customsbox .tablebox {
  clear: both;
  padding-top: 20px;
}
#Customsbox .pagebox{
  text-align: right;
  margin-top: 20px;
}
.Apply_btn{
  float: right;
}
</style>
<template>
  <div class="Customswindow" id="Customsbox">
    <p class="windowtitle title">装箱清单重构</p>
    <div>
      <ButtonGroup style="width:28%">
        <Input
          v-model.trim="key"
          placeholder="输入箱号/型号/品牌"
          clearable
          @on-clear="search_btn"
          style="width:80%;float:left;position: relative;left: 3px"
        />
        <!-- <Button style="float:left" @click="search_pro" type="primary">筛选</Button> -->
        <Button style="float:left" type="primary" @click="search_btn">筛选</Button>
      </ButtonGroup>
      <div class="Apply_btn">
        <!-- <Button type="text" icon="ios-create-outline" ghost @click="backpagetest">跳转测试</Button> -->
        <Button type="warning" icon="ios-create-outline" :disabled='Status==30?true:false'  @click="AskCustoms">报关申请</Button>
      </div>
    </div>
    <div class="tablebox">
      <Table :columns="columns" :data="reportList" :loading="loading" border @on-selection-change="selection_chenge">
        <template slot-scope="{ row, index }" slot="orderID">
          {{row.TinyOrderID}}
        </template>
        <template slot-scope="{ row, index }" slot="Boxcode">
           {{row.BoxCode}}
        </template>
        <template slot-scope="{ row, index }" slot="Entercode">
           {{row.EnterCode}}
        </template>
        <template slot-scope="{ row, index }" slot="BoxingDate">
           {{row.BoxingDate|showDate}}
        </template>
        <template slot-scope="{ row, index }" slot="Admin">
           {{row.Packer}}
        </template>
      </Table>
    </div>
    <div class="pagebox">
      <Page :total="Total" :current='PageIndex'/>
    </div>
    <!-- 申报窗口确认 开始 -->
    <Modal v-model="isdeclare" title="提示" @on-ok="ok_declare" @on-cancel="cancel_declare">
      <p>您是否确认申请报关？</p>
    </Modal>
    <!-- 申报窗口确认 结束-->
  </div>
</template>
<script >
//  库位，规格 是可选择的？（建议下拉选择可输入）
import $ from "jquery";
import {CgDelcarelist,CustomsApply} from "../../api/CgApi"
// import {GetUsableShelves,boxproducts,BoxingSpecs,GetBoxes,,ChangeBoxCode} from "../../api";
export default {
  name: "Customswindow",
  data() {
    return {
      key: "",
      Status:20,
      loading: true,
      isdeclare: false, //申报弹窗
      selectionarr: [], //全选与多选
      PageIndex:1,
      PageSize:6,
      Total:0,
      columns: [
        {
          type: 'selection',
          width: 60,
          align: 'center'
        },
        { title: "小订单号", slot: "orderID", align: "center",width:100 },
        { title: "箱号", slot: "Boxcode", align: "center",width:80},
        { title: "入仓号", slot: "Entercode", align: "center",width:100},
        { title: "装箱时间", slot: "BoxingDate", align: "center",width:100},
        { title: "装箱人", slot: "Admin", align: "center",width:100},
        {
          title: "型号",
          key: "list",
          align: "center",
          render: (h, params) => {
            return h(
              "div",
              {
                attrs: {
                  class: "subCol"
                }
              },
              [
                h(
                  "ul",
                  this.reportList[params.index].Items.map(item => {
                    return h("li", {}, item.Procut.PartNumber);
                  })
                )
              ]
            );
          }
        },
        {
          title: "品牌",
          key: "list",
          align: "center",
          render: (h, params) => {
            return h(
              "div",
              {
                attrs: {
                  class: "subCol"
                }
              },
              [
                h(
                  "ul",
                  this.reportList[params.index].Items.map(item => {
                    return h("li", {}, item.Procut.Manufacturer);
                  })
                )
              ]
            );
          }
        },
        {
          title: "申报数量",
          key: "list",
          align: "center",
          width:150,
          render: (h, params) => {
            return h(
              "div",
              {
                attrs: {
                  class: "subCol"
                }
              },
              [
                h(
                  "ul",
                  this.reportList[params.index].Items.map(item => {
                    return h("li", {}, item.Declare.Quantity);
                  })
                )
              ]
            );
          }
        }
      ],
      reportList2:[],
      reportList: []
    };
  },
  created() {
    this.setnva();
    this.getlist()
  },
   beforeRouteUpdate(to,from,next){
       this.Status=to.params.id;
       this.PageIndex=1,
       this.key='',
       this.getlist()
        next()
  },
  mounted() {},
  computed: {},
  methods: {
    setnva() {
      var cc = [
        {
          title: "报关申报",
          href: "/Transport/Customswindow"
        }
      ];
      this.$store.dispatch("setnvadata", cc);
    },
    getlist(){  //获取报关数据
      var data={
        PageIndex:this.PageIndex,//页码
        PageSize:this.PageSize,//单页条数
        Status:this.Status,//枚举请参考，目前我们只涉及20（未申报），30（已申报）
        // Packer:sessionStorage.getItem("userID"),//装箱人
        First:this.key//型号、厂商、箱号，陈翰觉得这样做不是很合理？
      }
        CgDelcarelist(data).then(res=>{
          console.log(res)
          this.Total=res.obj.Total;
          if(res.obj.Total>0){
              this.reportList=res.obj.Data;
            if(this.Status==30){
              this.reportList.forEach(item => {
                item._disabled =true;
              })
            }
          }else{
            this.reportList=[];            
          }
          this.loading=false;
        })
    },
    search_btn(){ //根据信号品牌筛选
      this.loading=true;
      this.PageIndex=1;
      this.getlist()
    },
    ok_declare() {
      //确认申请报关
      // console.log(this.selectionarr)

       var BoxIds=[]
      for (var i = 0; i < this.selectionarr.length; i++) {
        BoxIds.push(this.selectionarr[i].TinyOrderID);
      }
      console.log(BoxIds);
      CustomsApply(BoxIds).then(res => {
        if (res.val == 400) {
          this.$Message.error("申报失败，请重新操作");
        } else {
          this.$Message.success("申报成功");
          this.getlist();
        }
      });
    },
    cancel_declare() {
      //取消申请报关
    },
    selection_chenge(selection) {
      this.selectionarr = selection;
    },
    AskCustoms() {
      //报关申请
      if (this.selectionarr.length <=0) {
        this.$Message.error({
          content: "至少选择一个要申请报关的产品",
          duration: 1,
          closable: true,
          top: 50
        });
      } else {
        this.isdeclare = true;
      }
    },
    search_btn() {
      //筛选
      this.loading = true;
      this.getlist();
    },
    clear_search() {
      //清空筛选
      this.loading = true;
      this.getlist();
    }
  }
};
</script>




